import request from '../utils/request';

//修改密码
export function setPassword({...params}) {
    return request('/api/v1/account/password', {
        method: 'POST',
        body:{
            ...params
        }
    });
}
//获取服务器的配置信息列表（不分页）
export function serverPartList({...params}) {
    return request(`/api/v1/hardwareConfiguration/serverPartList`, {
        method: 'GET',
        body:{
            ...params
        }
    });
}
//获取询单硬件拓扑图
export function hardwareTopology({...params}) {
    return request(`/api/v1/order/hardwareTopology`, {
        method: 'GET',
        responseType: 'blob',
        body:{
            ...params
        }
    });
}
//查看订单详情
export function ordersDetails({...params}) {
    return request(`/api/v1/order/ordersDetails`, {
        method: 'GET',
        body:{
            ...params
        }
    });
}



