import React from 'react';
import {Menu, Icon, Popover, Form, Layout, Divider, Modal, Input, message} from 'antd';
import {withRouter,} from 'react-router-dom';
import  styles from './SenseHeader.scss';
import logoWhite from "../../assets/images/logo_white.png";
import avatar from '../../assets/images/avatar.svg';
import {setPassword} from "../../api/common";
import {toBASE64} from "../../utils/utils";
const { Header} = Layout;
const FormItem = Form.Item;
// import {IO_User} from '../../api/user.js'
class SenseHeader extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            stateUp:false,
            resetPasswordVisible:false,
            role:window.localStorage.getItem("role"),
            confirmDirty: false,
        };
    }
  async componentDidMount() {
        let key= this.props.history.location.pathname.split("/")[2]|| this.props.history.location.pathname.split("/")[1];
      console.log(this.props.history.location.pathname.split("/")[1]);
      this.props.itemTab.map((item) => {
          if(key===item.id){
              this.setState({current:key})
          }
          else if(key==="management"){
              this.setState({current:"account"})
          }
          else if(key==="sale"){
                  this.setState({current:"home"})
          }
          return item;
      });
    }

    handleVisibleChange=(visible)=>{
  if(!visible){
      this.setState({ stateUp:false})
  }
    };
    //注销登录
    logOut = ()=>{
        window.localStorage.clear();
        console.log(window.localStorage.getItem("token"));
        window.sessionStorage.clear();
        this.props.history.push({pathname: "/"});
        console.log( window.localStorage.getItem("role"));
    };
    //icon动画
    iconChange=()=>{
        this.setState({ stateUp:true})
    };
    //修改密码显示弹框
    setPassword=()=>{
    this.setState({ resetPasswordVisible:true})
    };
    //校验两次密码输入的是否一样
    compareToFirstPassword = (rule, value, callback) => {
        const form = this.props.form;
        if (value && value !== form.getFieldValue('newPassword')) {
            callback('两次密码输入的不一致!');
        } else {
            callback();
        }
    };
    handleConfirmBlur=(e)=>{
        const value = e.target.value;
        this.setState({ confirmDirty: this.state.confirmDirty || !!value });
    };
    validateToNextPassword = (rule, value, callback) => {
        const form = this.props.form;
        if (value && this.state.confirmDirty) {
            form.validateFields(['confirm'], { force: true });
        }
        callback();
    };
    //确认修改密码
    resetPasswordOk=()=>{
        this.props.form.validateFields(async(err, values)=>{
            if (!err) {
                console.log(values);
                try {
                   const info= await setPassword({
                        "oriPasswd":toBASE64(values.oldPassword),
                        "newPasswd":toBASE64(values.newPassword)
                    });
                    this.setState({ resetPasswordVisible:false})
                    if(info.errorCode === 1000) {
                        message.success(`修改密码成功`);
                    }
                    else if(info.errorCode === 1104){
                        message.error(`旧密码错误`);
                    }
                    else{
                        message.error(`修改密码失败`);
                    }
                }
                catch (e) {
                    message.error(`修改密码失败`);
                }
                this.props.form.resetFields();
            }
        });

    };
    //取消修改密码
    resetPasswordCancel=()=>{
        this.setState({ resetPasswordVisible:false});
        this.props.form.resetFields();
    };
    //tab切换
    handleClick = async (e) => {
        this.setState({current: e.key,});
        console.log(e.key);
        console.log();
        const fristRouter=this.props.location.pathname.split("/").splice(1,1).toString()
        this.props.history.push({pathname: `/${fristRouter}/${e.key}`});
    };
    //点击回到首页
    titleClick=()=>{
        if(this.state.role==="ROLE_BUSINESSMAN"){
            this.setState({current: "home",});
            this.props.history.push({pathname: `/sale`});
        }else{
            this.setState({current: "account",});
            this.props.history.push({pathname: `/management`});
        }
    };
    render() {
        // const {getFieldDecorator} = this.props.form;
        // window.localStorage.setItem("current",this.state.current);
        const { getFieldDecorator } = this.props.form;
        const dropDownContent = (
            <div>
                <div onClick={this.setPassword}>修改密码</div>
                <Divider style={{margin:"9px 0"}}/>
                <div onClick={this.logOut}>退出登录</div>
            </div>
        );
        const formItemLayout = {
            labelCol: {xs: { span: 6 }, sm: { span: 6 },},
            wrapperCol: { xs: { span: 15 }, sm: { span: 15},},
        };
        return (
            <Header className={styles.headerStyle}>
                <div className={styles.title} onClick={this.titleClick}>
                    <img width="111px" height="20px" src={logoWhite} alt="图片加载失败"/>
                    {
                       (this.state.role==="ROLE_BUSINESSMAN" )?
                        (<span>销售门户</span>):(<span>销售门户管理后台</span>)
                    }

                </div>
                <div className={styles.headerEnd}>
                    <Menu
                        selectedKeys={[this.state.current]}
                        mode="horizontal"
                        theme="dark"
                        onClick={this.handleClick}
                        style={{lineHeight: '60px'}}
                        className={styles.navTitle}
                    >
                        {
                            this.props.itemTab.map((item) => {
                                return (
                                    <Menu.Item
                                        key={item.id}
                                    ><span>{item.name}</span>
                                    </Menu.Item>)
                            })
                        }
                    </Menu>
                        <Popover
                            content={dropDownContent}
                            placement="bottomRight"
                            trigger="click"
                            onVisibleChange={this.handleVisibleChange}
                        >
                            <div className={styles.user} onClick={this.iconChange}>
                            <img width="24px" height="24px" src={avatar} alt="图片加载失败"/>
                            <span className={styles.name}>{window.localStorage.getItem("name")}</span>
                            <Icon type="caret-down" className={this.state.stateUp?styles.up:""}/>
                            </div>
                        </Popover>
                </div>
                {/*修改密码弹窗*/}
                <Modal
                    title="修改密码"
                    visible={this.state.resetPasswordVisible}
                    onOk={this.resetPasswordOk}
                    okText="确认"
                    cancelText="取消"
                    onCancel={this.resetPasswordCancel}
                >
                    <Form>
                        <FormItem {...formItemLayout} label="用户名">
                            <span>{window.localStorage.getItem("name")} {}</span>
                        </FormItem>
                        <FormItem
                            {...formItemLayout}
                            label="输入旧密码"
                        >
                            {getFieldDecorator('oldPassword', {
                                rules: [{
                                    required: true, message: '请输入旧密码!',
                                },{
                                    pattern: /^[^ ]+[\s\S]*[^ ]+$/,
                                    message: '密码前后不能有空格'
                                }],
                            })(
                                <Input type="password" />
                            )}
                        </FormItem>
                        <FormItem
                            {...formItemLayout}
                            label="输入新密码"
                        >
                            {getFieldDecorator('newPassword', {
                                rules: [{
                                    required: true, message: '请再次输入新密码!',
                                }, {
                                    validator: this.validateToNextPassword,
                                },{
                                    pattern: /^[^ ]+[\s\S]*[^ ]+$/,
                                    message: '密码前后不能有空格'
                                },{
                                    pattern:/^[^]{6,}/,
                                    message: '密码需6位数以上'
                                }

                                ],
                            })(
                                <Input type="password" placeholder="6位数以上支持英文，标点" />
                            )}
                        </FormItem>
                        <FormItem
                            {...formItemLayout}
                            label="确认新密码"
                        >
                            {getFieldDecorator('confirmPassword', {
                                rules: [{
                                    required: true, message: '请确认新密码!'
                                },{
                                    validator: this.compareToFirstPassword,
                                }],
                            })(
                                <Input type="password" onBlur={this.handleConfirmBlur} placeholder="6位数以上支持英文，标点"/>
                            )}
                        </FormItem>
                    </Form>
                </Modal>
            </Header>
        )
    }
}

export default  withRouter(Form.create()(SenseHeader))