import React from 'react';
import {Spin} from 'antd';
import {BrowserRouter, Route, Redirect, Switch, Link} from 'react-router-dom';
import Loadable from "react-loadable";
import SenseHeder from "./../components/header/SenseHeader";
const itemTab = [
    {name: "账号管理", id: "account"},
    {name: "询单管理", id: "queryForm"},
    {name: "硬件管理", id: "hardware"}
];
const businessmanItemTab = [
    {name: "首页", id: "home"},
    {name: "我的询单", id: "queryForm"},
];
class Router extends React.Component {
    // window.localStorage.getItem('token')
    render() {
        const PrivateRoute = ({component: Component,roles:roles,...rest}) => {
            return (
                <Route
                    {...rest}
                    render={props =>
                        window.localStorage.getItem('token')&& (roles.indexOf(localStorage.getItem("role"))) >=0  ? (
                          <Component {...props} />
                        ): (
                                <Redirect
                                    to={{
                                        pathname: "/login",
                                        state: {from: props.location}
                                    }}
                                />
                            )
                    }
                />
            );
        };
        const NoMatch = ({location}) => {
            return (
                <div>
                    <h1>404</h1>
                    <h3>
                        你要找的<code>{location.pathname}</code>页面不存在
                    </h3>
                    <Link to="/login">回到首页</Link>
                </div>
            );
        };
        return (
            <BrowserRouter>
                <Switch>
                    <Redirect exact={true} from="/" to="/login"/>
                    <Route path="/login" component={Loadable({
                        loader: () => import('../pages/login/Login'),
                        loading: () => <Spin/>,
                    })}/>
                    {/*<PrivateRoute path="/sale" component={Loadable({*/}
                    {/*loader: () => import('../pages/sale/Sale'),*/}
                    {/*loading: () => <Spin/>,*/}
                    {/*})}/>*/}
                    {/*<Route path="/management" component={Loadable({*/}
                    {/*loader: () => import('../pages/management/Management'),*/}
                    {/*loading: () => <Spin/>,*/}
                    {/*})}/>/>*/}
                    <PrivateRoute path="/sale" roles={["ROLE_BUSINESSMAN"]}  component={() => (
                        <div>
                            <SenseHeder itemTab={businessmanItemTab}/>
                            <Switch>
                                <Redirect exact={true} from="/sale" to="/sale/home"/>
                                <PrivateRoute path="/sale/home" roles={["ROLE_BUSINESSMAN"]} component={Loadable({
                                    loader: () => import('../pages/sale/home/Home'),
                                    loading: () => <Spin/>,
                                })}/>
                                <PrivateRoute path="/sale/queryForm" roles={["ROLE_BUSINESSMAN"]}  component={Loadable({
                                    loader: () => import('../pages/sale/queryForm/QueryForm'),
                                    loading: () => <Spin/>,
                                })}/>
                            </Switch>
                        </div>
                    )}/>
                    <PrivateRoute path="/management" roles={["ROLE_ADMIN"]} component={() => (
                        <div>
                            <SenseHeder itemTab={itemTab}/>
                            <Switch>
                                <Redirect exact={true} from="/management" to="/management/account"/>
                                <PrivateRoute path="/management/account" roles={["ROLE_ADMIN"]}  component={Loadable({
                                    loader: () => import('../pages/management/account/Account'),
                                    loading: () => <Spin/>,
                                })}/>
                                <PrivateRoute path="/management/hardware" roles={["ROLE_ADMIN"]} component={() =>
                                    <Switch>
                                        <Redirect exact={true} from="/management/hardware" to="/management/hardware/home"/>
                                        <PrivateRoute path="/management/hardware/home" roles={["ROLE_ADMIN"]} component={Loadable({
                                            loader: () => import('../pages/management/hardware/Hardware'),
                                            loading: () => <Spin/>,
                                        })}/>
                                        <PrivateRoute path="/management/hardware/part" roles={["ROLE_ADMIN"]} component={Loadable({
                                            loader: () => import('../pages/management/hardware/part/Part'),
                                            loading: () => <Spin/>,
                                        })}/>
                                    </Switch>
                                }/>
                                <PrivateRoute path="/management/queryForm" roles={["ROLE_ADMIN"]} component={Loadable({
                                    loader: () => import('../pages/management/queryForm/QueryForm'),
                                    loading: () => <Spin/>,
                                })}/>
                            </Switch>
                        </div>
                    )}/>
                    <Route component={NoMatch}/>
                </Switch>
            </BrowserRouter>
        )
    }

}

export default Router;